@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html{box-sizing:border-box}header{background-color:#db4c3f;border-bottom:solid 1px #ca2100;box-shadow:0 1px 2px rgba(0,0,0,0.15);height:44px;width:100%;font-size:larger}header nav{display:flex;margin:auto;height:100%;max-width:922px;justify-content:center}body{margin:0;padding:0;line-height:1;font-family:"Roboto",sans-serif;color:black;background-color:#fafafa}.content{display:flex;max-width:922px;margin:auto;justify-content:center}.sidebar{-webkit-user-select:none;-ms-user-select:none;user-select:none;height:100vh;width:266px;padding-top:70px;background-color:#fafafa;border-right:1px solid #f1f1f1}.sidebar div{margin-bottom:10px;cursor:pointer;padding:15px}.sidebar div.active{background-color:white}.sidebar div .icon{margin-right:10px}.tasks{padding:60px 20px 0;height:100vh;width:calc(922px-266px)}.tasks .add-btn-task{cursor:pointer}.tasks .plus{color:red;padding-right:10px}.tasks .add-task-text{color:#545454;font-size:14px}.add-tsk-dial{margin-top:10px}.add-tsk-dial input{width:150%;border:1px solid #ddd;background-color:white;border-radius:3px;height:35px;padding-right:10px;padding-left:10px}.add-tsk-dial .icon-container{width:20%}.add-tsk-dial .add-task-actions-container{margin-top:10px;display:flex;justify-content:space-between}.add-tsk-dial .add-task-actions-container .add_tsk{background-color:#db4c13;margin-right:10px;font-weight:bold;color:white}.add-tsk-dial .add-task-actions-container .add_tsk:disabled{background-color:grey}.add-tsk-dial .add-task-actions-container .add_tsk:active{-webkit-transform:scale(0.9);transform:scale(0.9)}.add-tsk-dial .add-task-actions-container .cnc{all:unset;cursor:pointer}

